import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import React, { useState } from "react";
import { api } from "../../modules/api";
import * as S from "./styles";
import Modal from "react-bootstrap/Modal";
import Btn from "../Buttons";
import { useNavigate } from "react-router-dom";

const schema = yup
  .object({
    name: yup.string().required(),
    surname: yup.string().required(),
    office: yup.string().required(),
    email: yup.string().required(),
  })
  .required();

export default function Inputs() {
  const [isModal, setIsModal] = useState(false);
  const [isError, setIsError] = useState({
    email: undefined,
  });
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm({
    resolver: yupResolver(schema),
  });

  const headers = {
    headers: {
      "Content-Type": "application/json",
    },
  };

  const onSubmit = (data) => {
    api
      .post("/create-account", data, headers)
      .then((response) => {
        if (response.status === 200) {
          setIsModal(true);
          console.log(data);
        }
      })
      .catch((error) => {
        console.log(error);
        if (error?.response?.data?.message === "E-mail already registered") {
          setIsError({
            ...isError,
            email: "E-mail already registered.",
          });
        }
      });
  };
  const navigate = useNavigate();

  const renderModal = () => {
    const handleClose = () => {
      navigate("/");
      setIsModal(false);
    };
    return (
      <Modal show={isModal} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Successfully registered</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          In a few moments you will receive the available vacancies in your
          email.
        </Modal.Body>
        <Modal.Footer>
          <Btn variant="primary" onClick={handleClose}>
            Close
          </Btn>
        </Modal.Footer>
      </Modal>
    );
  };

  return (
    <>
      <S.Form onSubmit={handleSubmit(onSubmit)}>
        <S.BoxInput>
          <label htmlFor="name">Name</label>
          <input {...register("name")} placeholder="Name" />
          <p>{errors.name?.message}</p>
        </S.BoxInput>
        <S.BoxInput>
          <label htmlFor="surname">Last Name</label>
          <input {...register("surname")} placeholder="Last Name" />
          <p>{errors.surname?.message}</p>
        </S.BoxInput>
        <S.BoxInput>
          <label htmlFor="office">Role</label>
          <input {...register("office")} placeholder="Role" />
          <p>{errors.office?.message}</p>
        </S.BoxInput>
        <S.BoxInput>
          <label htmlFor="email">Email</label>
          <input {...register("email")} placeholder="Email" />
          <p>{errors.email?.message}</p>
          <p>{isError.email}</p>
        </S.BoxInput>
        <Btn>Submit</Btn>
      </S.Form>
      {isModal && renderModal()}
    </>
  );
}
